import React from 'react';
import '../navbar/navbar.css';
import {
    BrowserRouter as Router,
    Switch,
    Route,
    Link
  } from "react-router-dom";

function Navbar() {
    return (
        <Router>
        <div className="navbar">
            <div className="logo">
            <img src="https://www.vlaminck14.be/images/templates/images/logo-20191118114309.png"/>
            </div>

            <ul className="nav__links">
                <li className="nav__link">
                    
                    <a href="/">Home</a>
                   
                </li>

                <li className="nav__link">
                   
                    <a href="/menu">Our Menu</a>
                    
                </li>

                <li className="nav__link">
                    <a href="/opening-hours">Opening Hours</a>
                </li>

                <li className="nav__link">
                    <a href="/impression">Impression</a>
                </li>

                <li className="nav__link">
                    <a href="/book-a-table">Book A Table</a>
                </li>
                
            </ul>

        </div>
        </Router>
    )
}

export default Navbar
