import react from 'react';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";
import Navbar from './components/navbar/Navbar';
import Home from './components/home/Home';
import MenuShow from './components/ourMenu/MenuShow';
import Impression from './components/impression/Impression';
import BookTable from './components/booktable/BookTable';
import OpeningHours from './components/openinghours/OpeningHours';
import './app.css';

function App() {
  return (
    
   <Router>
     <div className='app'>
     <Switch>
       
     <Route path="/book-a-table">
         <Navbar />
         <BookTable />
       </Route>

     <Route path="/impression">
     <Navbar />
         <Impression />
       </Route>

     <Route path="/opening-hours">
     <Navbar />
         <OpeningHours />
       </Route>

     <Route path="/menu">
         <MenuShow />
       </Route>
     <Route path="/">
         <Home />
       </Route>

       <Route path="/menu">
         <MenuShow />
       </Route>

      

     </Switch>
     </div>
   </Router>
  
  )
};

export default App;
