import React from 'react';
import './openinghours.css';

function OpeningHours() {
    return (
        <div className="opening__container">
            <h1>Opening Hours</h1>
            
            <div className="opening__heading">
              
                <ul className="days">
                 
                 <li>Monday</li>
                 <li>Tuesday</li>
                 <li>Wednesday</li>
                 <li>Thursday</li>
                 <li>Friday</li>
                 <li>Saturday</li>
                 <li>Sunday</li>
                </ul>

                <ul className="time">
                 <li>11 AM - 3PM, 6 PM - 10PM</li>
                 <li>11 AM - 3PM, 6 PM - 10PM</li>
                 <li>CLOSED   </li>
                 <li>11 AM - 3PM, 6 PM - 10PM  </li>
                 <li>11 AM - 3PM, 6 PM - 10PM</li>
                 <li>11 AM - 3PM, 6 PM - 10PM </li>
                 <li>11 AM - 3PM, 6 PM - 10PM</li>
                </ul>

              
            </div>
        </div>
    )
}

export default OpeningHours
