import React from 'react';
import './booktable.css'

function BookTable() {
    return (
        <div className="main">
            
        <div className="form__container">
            <h2 className="form__title">book your table now </h2>
            <form className="form__inputs">
                
                <div className="form__input">
                <label for="name" className="form__label">Name </label>
                <input type="text" className="form__control" placeholder="Enter Name"/>
                </div>

                <div className="form__input">
                <label for="date" className="form__label">Date </label>
                <input type="date" className="form__control" placeholder="Select Date"/>
                </div>

                <div className="form__input">
                <label for="no-of-people" className="form__label">Guest</label>
                <input type="text" className="form__control" placeholder="How many people ?"/>
                </div>

                <div className="form__input">
                <label for="name" className="form__label">Request </label>
                <textarea type="text" className="form__control" placeholder="special arrangements ?"/>
                </div>

                <div className="form__input">
                <button type="submit" className="form__btn" value="Book Now">Book Now</button>
                </div>
            </form>
            
        </div>
        </div>
    )
}

export default BookTable
